@import "libs";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700;800;900&display=swap');
html {
	height: 100%;
}
body {
	background: #0E0E0E;
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	color: #444;
	height: 100%;
	user-select: none;
}
.wrapper {
	max-width: 1920px;
	margin: 0 auto;
	font-size: 0;
	width: 100%;
	height: 100%;
	max-height: 1084px;
	position: absolute;
	left: 50%;
	overflow: hidden;
	top: 50%;
	transform: translate(-50%,-50%);
	.logo{
		z-index: 9;
		width: 175px;
		height: 121px;
		position: absolute;
		bottom: 30px;
		left: 50%;
		transform: translateX(-50%);
		background: url(../img/logo.png);
		@media screen and (max-width: 1344px) {
			transform: scale(0.5) translateX(-50%);
			left: 45%;
		}
	}
	.textBox {
		opacity: 0;
		pointer-events: none;
		transition-duration: 100ms;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		color: #fff;
		text-align: center;
		img {
			display: inline-block;
			text-align: center;
			margin-bottom: 10px;
		}
		.title {
			text-align: left;
			display: block;
			font-weight: 600;
			font-size: 48px;
			margin-bottom: 20px;
			@media screen and (max-width: 1025px) {
			    font-size: 38px;
			}
		}
		ul {
			text-align: left;
			li {
				display: block;
				margin-bottom: 20px;
				span {
					padding-bottom: 15px;
					display: inline-block;
					font-size: 24px;
					border-bottom: 1px solid #969696;
					@media screen and (max-width: 1025px) {
						font-size: 20px;
					}
				}
			}
		}
	}
	.redBtn {
		border-radius: 6px;
	    padding: 10px 40px;
	    font-size: 16px;
	    text-decoration: none;
	    color: #fff;
	    text-transform: uppercase;
	    background: #911f1f;
	    box-shadow: 0px 0px 9px #ff000052;
	    float: right;
	    transition-duration: 200ms;
	    cursor: pointer;
		@media screen and (max-width: 1025px) {
			padding: 10px 20px;
		}
	}
	.blueBtn {
		border-radius: 6px;
	    padding: 10px 40px;
	    font-size: 16px;
	    text-decoration: none;
	    color: #fff;
	    text-transform: uppercase;
	    background: #2178fc;
	    box-shadow: 0px 0px 9px #2178fc85;
	    float: right;
	    transition-duration: 200ms;
	    cursor: pointer;
		@media screen and (max-width: 1025px) {
			padding: 10px 20px;
		}
	}
	.blueBtn:hover,
	.redBtn:hover {
		opacity: 0.9;
	}
	.wrapper_firstBg {
		max-width: 1920px;
		position: absolute;
		width: 100%;
		left: 50%;
		top: 50%;
		background: url(../img/defBg.jpg) no-repeat center;
		transform: translate(-50%,-50%);
		height: 100%;
		background-size: cover;
		display: block;
		opacity: 1;
		z-index: 10;
		transition-duration: 300ms;
		.hoverBlock {
			width: 70%;
			height: 200px;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			cursor: pointer;
		}
	}
	.wrapper_firstBg.dis {
		opacity: 0;
		pointer-events: none;
	}
	.wrapper_bgLeft {
		font-size: 15px;
	    background: url(../img/bgLeft.png) no-repeat;
	    background-position: 97%;
	    max-width: 58%;
	    height: 100%;
	    width: 100%;
	    display: inline-block;
	    vertical-align: middle;
	    position: absolute;
	    left: 0;
	    top: 0;
	    z-index: 9;
	    filter: grayscale(100%); 
	    transition-duration: 200ms;
	    cursor: pointer;
	    .textBox {
	    	left: 57%;
	    }
	    .labelP {
			position: absolute;
			bottom: 135px;
			left: 19%;
			// transition-duration: 200ms;
			transform: scale(0.85) translateX(-50%);
			@media screen and (max-width: 1344px) {
				transform: scale(0.5) translateX(-50%);
				left: 0%;
			}
	    }
	    .labelText {
			position: absolute;
			left: 30%;
			transform: scale(1) translateX(-50%);
			bottom: 50px;
			max-width: 400px;
			@media screen and (max-width: 1344px) {
				left: 9%;
				bottom: 200px;
				transform: scale(0.50) translateX(-50%);
			}
	    }
	}
	.wrapper_bgLeft.dis,
	.wrapper_bgRight.dis {
		z-index: 2;
	}
	.wrapper_bgLeft.dis {
		.labelText {
			opacity: 0.2;
		}
	}
	.wrapper_bgLeft.active {
		filter: grayscale(0);
		opacity: 1;
		max-width: 87% !important;
		z-index: 6;
	}
	.wrapper_bgLeft:hover {
		filter: grayscale(0);
	}
	.wrapper_bgRight {
		font-size: 15px;
	    background: url(../img/bgRight.png) no-repeat;
	    background-position: 3%;
	    max-width: 63%;
	    height: 100%;
	    width: 100%;
	    display: inline-block;
	    vertical-align: middle;
	    position: absolute;
	    right: 0;
	    top: 0;
		z-index: 9;
		filter: grayscale(100%); 
		transition-duration: 200ms;
		cursor: pointer;
		.textBox {
			left: 38%;
		}
	    .labelP {
			position: absolute;
			bottom: 100px;
			right: -32%;
			// transition-duration: 200ms;
			transform: scale(0.85) translateX(-50%);
			@media screen and (max-width: 1344px) {
				transform: scale(0.5) translateX(-50%);
				right: -40%;
			}
			@media screen and (max-width: 1170px) {
				right: -50%;
			}
	    }
	    .labelText {
			position: absolute;
			right: 10%;
			transform: scale (0.85) translateX(-50%);
			top: 50px;
			max-width: 266px;
			// transition-duration: 200ms;
			@media screen and (max-width: 1344px) {
				right: -10%;
				top: 170px;
				transform: scale(0.50) translateX(-50%);
			}
	    }
	}
	.wrapper_bgRight.dis {
		.labelText {
			opacity: 0.2;
		}
	}
	.bgLeftEnder {
	    width: 250px;
	    height: calc(100% + 500px);
	    display: inline-block;
	    vertical-align: middle;
	    position: absolute;
	    top: -100px;
	    right: 60px;
	    transform: rotate(20deg);
	}
	.bgRightEnder {
	    width: 250px;
	    height: calc(100% + 500px);
	    display: inline-block;
	    vertical-align: middle;
	    position: absolute;
	    top: -100px;
    	left: -129px;
	    transform: rotate(20deg);
	}
	.wrapper_bgRight.active {
		filter: grayscale(0);
		opacity: 1;
		max-width: 83% !important;
		z-index: 6;
		.textBox {
			opacity: 1;
			pointer-events: unset;
		}
		.labelText {
			transform: scale(0.85) translateX(-50%);
			left: 35.5%;
			right: unset;
			@media screen and (max-width: 1344px) {
				transform: scale(0.30) translateX(-120%);
				top: 40px;
			}
		}
		.labelP {
			transform: scale(1);
			right: 0;
			bottom: -150px;
			@media screen and (max-width: 1344px) {
				transform: scale(0.4);
				bottom: -250px;
				right: -220px;
			}
		}
		.bgRightEnder {
			width: 100px;
			left: 40px;
		}
	}
	.wrapper_bgLeft.active {
		.textBox {
			opacity: 1;
			pointer-events: unset;
		}
		.labelText {
			transform: scale (0.85) translateX(-50%);
			left: 56.5%;
			top: 60px;
			right: unset;
			@media screen and (max-width: 1344px) {
				transform: scale(0.35) translateX(-115%);
				top: 40px;
			}
		}
		.labelP {
			transform: scale(1);
			left: 0;
			bottom: -150px;
			@media screen and (max-width: 1344px) {
				transform: scale(0.4);
				left: -200px;
				bottom: -200px;
			}
		}
		.bgLeftEnder {
			width: 100px;
			right: 140px;
		}
	}
	.wrapper_bgRight:hover {
		filter: grayscale(0);
	}
}

@media screen and (min-width: 850px) {
	.wrapperUpMob,
	.wrapperDownMob {
		display: none;
	}
}

@media screen and (max-width: 850px) {
	.wrapper {
		.wrapper_firstBg .hoverBlock {
			width: 100%;
			height: 100%;
		}
		.wrapper_bgLeft,
		.wrapper_bgRight,
		.logo {
			display: none;
		}
		.wrapper_firstBg {
			background: url(../img/defBgMob.jpg) no-repeat center;
			background-size: cover;
		}
		.wrapperUpMob,
		.wrapperDownMob {
			display: block;
			height: 50%;
			width: 100%;
			filter: grayscale(100);
			transition-duration: 300ms;
		}
		.wrapperDownMob.dis,
		.wrapperUpMob.dis {
			pointer-events: none;
		}
		.wrapperUpMob.active {
			filter: grayscale(0);
			height: 100%;
			.textBox {
				opacity: 1;
				width: 100%;
			    padding: 0 20px;
			    pointer-events: unset;
			    .title {
			    	font-size: 25px;
			    	display: block;
			    	text-align: center;
			    }
			    ul {
			    	li {
			    		span {
			    			font-size: 16px;
			    		}
			    	}
			    }
			    .backLink {
			    	position: absolute;
			    	top:0px;
			    	left: 10px;
			    	cursor: pointer;
			    	max-width: 15px;
			    }
			}
		}
		.wrapperDownMob.active {
			filter: grayscale(0);
			height: 100%;
			position: absolute;
			top: 0;
			.textBox {
				opacity: 1;
				width: 100%;
			    padding: 0 20px;
			    pointer-events: unset;
			    .title {
			    	font-size: 25px;
			    	display: block;
			    	text-align: center;
			    }
			    ul {
			    	li {
			    		span {
			    			font-size: 16px;
			    		}
			    	}
			    }
			    .backLink {
			    	position: absolute;
			    	top:0px;
			    	left: 10px;
			    	cursor: pointer;
			    	max-width: 15px;
			    }
			}
		}
		.wrapperDownMob:hover,
		.wrapperDownMob:active,
		.wrapperUpMob:hover,
		.wrapperUpMob:active {
			filter: grayscale(0);
		}
		.wrapperUpMob {
			background: url(../img/mobru.jpg) no-repeat left bottom;
			background-size: cover;
		}
		.wrapperDownMob {
			background: url(../img/mobus.jpg) no-repeat right bottom;
			background-size: cover;
		}
	}
}

.preloaderBar {
	position: absolute;
	background: #181818;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	opacity: 1;
	pointer-events: unset;
	transition-duration: 300ms;
	.preloader {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		max-width: 100px;
		width: 100%;
	}
}
.preloaderBar.disabled {
	opacity: 0;
	pointer-events: none;
}